<template>
	<div id="editAddress">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">编辑收货地址</div>
        <div class="van-nav-bar__right" @click="deleteAddress">
          删除
        </div>
      </div>
    </div>
    <div class="login_box">
      <div class="van-cell-group van-hairline--top-bottom">
        <div class="van-cell van-hairline van-field">
          <div class="van-cell__value">
            <div class="van-field__body">
              <input type="text" maxlength="15" placeholder="收货人" class="van-field__control" v-model="username">
            </div>
          </div>
        </div>
        <div class="van-cell van-hairline van-field">
          <div class="van-cell__value">
            <div class="van-field__body">
              <input type="tel" placeholder="手机号码" class="van-field__control" v-model="telephone">
            </div>
          </div>
        </div>
        <div class="van-cell van-hairline van-field">
          <div class="van-cell__value">
            <div class="van-field__body">
              <input type="text" placeholder="地区" class="van-field__control" v-model="area">
            </div>
          </div>
        </div>
        <div class="van-cell van-hairline van-address-edit-detail">
          <div class="van-cell__value van-cell__value--alone">
            <div class="van-cell van-hairline van-field">
              <div class="van-cell__value">
                <div class="van-field__body">
                  <textarea rows="1" maxlength="200" placeholder="详细地址" class="van-field__control" style="height: 24px;" v-model="detailaddress"></textarea>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="van-cell van-cell--center van-switch-cell">
          <div class="van-cell__title">
            <span>设为默认收货地址</span>
          </div>
          <div class="van-cell__value">
            <van-checkbox v-model="checked"></van-checkbox>
          </div>
        </div>
      </div>
      <div style="padding: 20px;">
        <button class="login_btn" @click="editAddress">修改</button>
      </div>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'editAddress',
		data() {
			return {
        checked:true,
        addressInfo:[],
        userId:localStorage.getItem('userId'),
        area:'',
        detailaddress:'',
        username:'',
        telephone:'',
			}
		},
    mounted() {
      this.addressInfo = this.$route.query.data;
      this.area = this.addressInfo.area;
      this.detailaddress = this.addressInfo.detailaddress;
      this.username = this.addressInfo.username;
      this.telephone = this.addressInfo.telephone;
      if(this.addressInfo.type=='0'){
        this.checked = false;
      }
    },
		methods:{
      deleteAddress(){
        let $this = this;
        let obj = {
          userid: $this.userId,
          addressid:$this.addressInfo.addressid,
        };
        let qs = require('qs');
        $this.$ajax.post("address/deleteaddress", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.$toast({
              message: '删除地址成功!',
              position: "bottom"
            });
            $this.$router.back(-1);
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
      editAddress(){
        let type = 1;
        if(!this.checked){
          type = 0;
        }
        let $this = this;
        let obj = {
          userid: $this.userId,
          addressid:$this.addressInfo.addressid,
          type: type,
          area: $this.area,
          detailaddress: $this.detailaddress,
          username: $this.username,
          telephone: $this.telephone,
        };
        let qs = require('qs');
        $this.$ajax.post("address/updateaddress", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.$toast({
              message: '修改地址成功!',
              position: "bottom"
            });
            $this.$router.back(-1);
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
		}
	}
</script>
<style scoped>
  .login_btn{
    margin-top: 10%;
    background-color: #c10804;
    width: 100%;
    height: 44px;
    border-radius: 8px;
    color: #ffffff;
    border: 0;
  }
  .login_btn{
    width: 96%;
    margin-left: 2%;
  }
  .login_box{
    margin-top: 44px;
  }
</style>



